import React from 'react';
import './visualizerComponent.css'
import {bubbleSort, insertionSort,selectionSort} from './sortingAlgorithms/algorithms'


export interface Props{
  
}

interface settings {
  maxValue: number;
  minValue: number;
  arraySize: number;
  barColor: string;
  activeBarColor: string;
  animationSpeed: number
}

export interface animationStep{
  bar1: number;
  bar2: number;
  bars: number[];
  swap: number;
}



export default class VisualizerComponent extends React.Component<{},{array : number[],timeouts : any[],isSorted : boolean}>{

    private settings : settings = {
      maxValue : 900,
      minValue : 15,
      arraySize: 200,
      barColor: "green",
      activeBarColor: "red",
      animationSpeed: .5,
    }
    
  

    private animation : animationStep [] = [];

    constructor(props:Props) {
        super(props);
        this.state = {
          array : this.randomizeArray(),
          timeouts : [],
          isSorted : false
        }
    }
      

    //Randomize the values in array
    randomizeArray(){
      let newArray : number[] = [];
      for(let i = 0; i < this.settings.arraySize;i++){
        newArray.push(Math.floor(Math.random() * (this.settings.maxValue - this.settings.minValue)) + this.settings.minValue)
      }
      return newArray
    }
  
    resetArray = () => {
      while(this.state.timeouts.length !== 0){
        clearTimeout(this.state.timeouts[0])
        this.state.timeouts.shift();
      }
      this.resetBars()
      this.setState({array:this.randomizeArray(),isSorted:false})
    }

    resetBars = () => {
      let arraybars = document.getElementsByClassName("array-bar") as HTMLCollectionOf<HTMLElement>;
      for(let i = 0; i < arraybars.length;i++){
        arraybars[i].style.backgroundColor = this.settings.barColor;
      }
    }

    runAlgorithm = (algorithm : (arr:number[]) => any) =>{
      if(this.state.timeouts.length === 0){
        let copy = [...this.state.array]
        this.animation = algorithm(copy)
        this.runAnimation()
        this.setState({isSorted:true})
      }
    }

    runAnimation(){
      let prevBar1 : number = -1;
      let prevBar2 : number = -1;
      let arraybars = document.getElementsByClassName("array-bar") as HTMLCollectionOf<HTMLElement>;
      for(let i = 0; i < this.animation.length;i++){
        this.state.timeouts.push(setTimeout(()=>{
          if(prevBar1 > -1) arraybars[prevBar1].style.backgroundColor = this.settings.barColor;
          if(prevBar2 > -1) arraybars[prevBar2].style.backgroundColor = this.settings.barColor;
          this.animation[i].bars.forEach(barID => {
            arraybars[barID].style.backgroundColor = this.settings.activeBarColor
          });
          // arraybars[this.animation[i].bar1].style.backgroundColor = this.settings.activeBarColor
          // arraybars[this.animation[i].bar2].style.backgroundColor = this.settings.activeBarColor          
          
          if(this.animation[i].swap === 1){

            let newArray = this.state.array
            let temp = newArray[this.animation[i].bar1]
            newArray[this.animation[i].bar1] = newArray[this.animation[i].bar2]
            newArray[this.animation[i].bar2] = temp
           
            this.setState({array : newArray})
          }
          
          prevBar1 = this.animation[i].bar1
          prevBar2 = this.animation[i].bar2          
          this.state.timeouts.shift();
          if(this.state.timeouts.length <= 0){
            this.setState({isSorted:true})
          }
        }, i * this.settings.animationSpeed))
      }
    }


    render(){

      var list : number[] = this.state.array;
      const pColor : string = this.settings.barColor;
      let copy = [...this.state.array]

      return <div>
        <div className = "array-container">
          {list.map((val,idx)=>(
            <div
              className = "array-bar"
              key = {idx}
              id = {idx.toString()}
              style = {{
                backgroundColor :`${pColor}`,
                height : `${val}px`
              }}></div>
          ))}
          </div>


        <div className = "button-container">

          <div className="dropup">
            <button className="dropbtn">Algorithms</button>
              <div className="dropup-content">
                  <button 
                    disabled = {this.state.isSorted} 
                    className="buttons" 
                    onClick={() => {
                      if(this.state.timeouts.length === 0){
                        this.animation = bubbleSort(copy)
                        this.runAnimation()
                        this.setState({isSorted:true})
                      }
                  }}>Bubble Sort</button>
                    
                  <button 
                    disabled = {this.state.isSorted} 
                    className="buttons" 
                    onClick={() => {
                      if(this.state.timeouts.length === 0){
                        this.animation = insertionSort(copy)
                        this.runAnimation()
                        this.setState({isSorted:true})
                      }
                  }}>Insertion Sort</button>

                  <button 
                    disabled = {this.state.isSorted} 
                    className="buttons" 
                    onClick={() => {
                      if(this.state.timeouts.length === 0){
                        this.animation = selectionSort(copy)
                        this.runAnimation()
                        this.setState({isSorted:true})
                      }
                  }}>Selection Sort</button>
                
              </div> {/*dropup-content*/}
            </div> {/*Dropup*/}


            
            <button id="resetBtn" onClick={this.resetArray}>Reset </button>
          </div>{/* Button Container */} 

      </div>
    }
}